import WxValidate from '../../../utils/WxValidate.js'
import uniPopup from "../../../components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			listData:[
				{title:'广告推销',tip:['房产','推广','保险','车险']},
				{title:'外卖快递',tip:['外卖','快递','生活服务']},
				{title:'商务电话',tip:['开会','预约','商务']},
				{title: '贷款催收',tip:['催收','推广','贷款']}
			],
			mobileCode: "获取验证码",
			time: 60,
			mobileTip: "",
			inputValue: "",
			smsTip: "",
			mobile: "",
			smsCode: "",
			isDisable: false,
			bindmobile: '',
			isCheck: false,
			checked: 'agree'
		}
	},
	onLoad() {
		this.initValidate();
	},
	methods: {
		userAgree(){
			uni.navigateTo({
				url:'../../../static/privacyAgreement/privacyAgreement'
			})
		},
		privacyAgree(){
			uni.navigateTo({
				url:'../../../static/userAgreement/userAgreement'
			})
		},
		// 规则校验
		initValidate() {
			// 规则
			const rules = {
				mobile: {
					required: true,
					tel: true
				},
				smsCode: {
					required: true
				}
			}
			// 提示
			const messages = {
				mobile: {
					required: "请输入手机号！"
				},
				smsCode: {
					required: "请输入验证码"
				}
			}
			this.WxValidate = new WxValidate(rules, messages)
		},
		// 获取验证码
		getMobileCode() {
			if (this.bindmobile) {
				if (this.mobileCode == '获取验证码') {
					this.time = 60
					this.timer()
					var jiamiMobile = this.$encruption(this.bindmobile)
					const params = {
						mobile: this.bindmobile,
						elmobi: jiamiMobile
					}
					console.log(params)
					this.$request.url_request(SENDCODE, params, 'POST', res => {
						uni.showToast({
							title: '验证码发送成功',
							icon: 'none',
							duration: 1000
						})
					}, err => {})
				}
			} else {
				uni.showToast({
					title: '手机号不可为空',
					icon: 'none',
					duration: 1000
				})
			}
		
		
		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.mobileCode = "还剩" + this.time + "s"
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.mobileCode = "获取验证码";
			}
		},
		// 登录
		handleLogin(e) {
			var that = this
			this.isDisable = true
			setTimeout(function() {
				that.isDisable = false
				that.$forceUpdate()
			}, 1000)
			const params = e.detail.value
			if (!this.WxValidate.checkForm(params)) {
				const error = this.WxValidate.errorList[0]
				// 可以自定义提示
				uni.showToast({
					title: `${error.msg}`,
					icon: 'none'
				});
				return false
			} else {
				if(this.isCheck == false){
					uni.showToast({
						title:'请阅读隐私协议',
						icon:'none',
						duration:1200
					})
				} else{
					
				}
				var _this = this
				this.$request.url_request(LOGIN, params, "POST", res => {
					if (JSON.parse(res.data).code == 0) {
						uni.navigateTo({
							url: '../../Welcome/Welcome'
						})
					} else {
						if (JSON.parse(res.data).msg) {
							uni.showToast({
								title: JSON.parse(res.data).msg,
								icon: 'none',
								duration: 1000
							})
						}
					}
				}, err => {
		
				});
			}
		},
		backtohome(){
			uni.navigateTo({
				url:'../bindMobile'
			})
		},
		open() {
			this.$refs.popup.open()
		},
		checkboxChange(value) {
			this.isCheck = !this.isCheck
		},
		detail(index){
			console.log(index)
			if(index == 0){
				uni.navigateTo({
				url:'../callListDetail/callListDetail'
			})
			} else if(index == 1){
				uni.navigateTo({
					url:'../Courier/Courier'
				})
			} else if(index == 2){
				uni.navigateTo({
					url:'../business/business'
				})
			} else if(index == 3){
				uni.navigateTo({
					url:'../collection/collection'
				})
			}
			
		}
	}
}
